import React from 'react';
import {
  Image,
  ImageSourcePropType,
  StatusBar,
  StyleProp,
  TextStyle,
  View,
  ViewStyle,
} from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import ReactNativeModal from 'react-native-modal';
import { styles } from './styles';
import colors from '../../../theme/colors';
import { warning_icon } from '../../../assets/images';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/store/rootReducer';
import { hideConfirmModal } from '../../../redux/features/app/actions';

export interface ConfirmModalProps {
  visible?: boolean;
  title?: string;
  description?: string;
  confirmText?: string;
  cancelText?: string;
  icon?: ImageSourcePropType;
  containerStyle?: StyleProp<ViewStyle>;
  footerStyle?: StyleProp<ViewStyle>;
  iconSize?: number;
  titleStyle?: StyleProp<TextStyle>;
  descriptionStyle?: StyleProp<TextStyle>;
  customContent?: React.ReactNode;
  isShowIcon?: boolean;
  statusBarColorHideModal?: string;
  statusBarBarStyleHideModal?: 'light-content' | 'dark-content';
  statusBarBarStyleShowModal?: 'light-content' | 'dark-content';
  statusBarColorShowModal?: string;
  iconTintColor?: string;
  onConfirm?: () => void;
  onCancel?: () => void;
}

const ConfirmModal = () => {
  const stateModal = useSelector((state: RootState) => state.app.confirmModal);
  const dispatch = useDispatch();

  const {
    visible,
    title,
    description,
    confirmText,
    cancelText,
    icon,
    iconSize = SCREEN_WIDTH * 0.08,
    titleStyle,
    footerStyle,
    customContent,
    isShowIcon,
    descriptionStyle,
    containerStyle,
    statusBarColorHideModal = colors.white,
    statusBarBarStyleHideModal = 'dark-content',
    statusBarBarStyleShowModal = 'light-content',
    statusBarColorShowModal = colors.grayScale80,
    iconTintColor = colors.mainColorPrimary,
    onConfirm,
    onCancel,
  } = stateModal;

  const handleClose = () => {
    if (onCancel) {
      onCancel();
    } else {
      dispatch(hideConfirmModal());
    }
  };

  return (
    <ReactNativeModal
      animationIn="zoomIn"
      animationOut="zoomOut"
      animationInTiming={400}
      animationOutTiming={400}
      style={styles.modal}
      onModalShow={() => {
        // Khi modal mở → đổi màu status bar
        StatusBar.setBarStyle(statusBarBarStyleShowModal);
        StatusBar.setBackgroundColor(statusBarColorShowModal);
      }}
      onModalHide={() => {
        // Modal đóng → trả về bình thường
        StatusBar.setBarStyle(statusBarBarStyleHideModal);
        StatusBar.setBackgroundColor(statusBarColorHideModal);
      }}
      backdropOpacity={0.5}
      backdropColor={colors.black}
      isVisible={visible}
      useNativeDriver
    >
      <View style={styles.container}>
        {customContent ? (
          customContent
        ) : (
          <View style={[styles.contentContainer, containerStyle]}>
            <Button style={styles.closeButton} variant="ghost">
              <Text style={styles.closeButtonText}>✕</Text>
            </Button>

            <View style={styles.headerContainer}>
              {isShowIcon && (
                <View
                  style={[
                    styles.iconWarningContainer,
                    { width: iconSize * 1.8, height: iconSize * 1.8 },
                  ]}
                >
                  <Image
                    tintColor={iconTintColor}
                    source={icon ? icon : warning_icon}
                    style={{
                      width: iconSize,
                      height: iconSize,
                    }}
                  />
                </View>
              )}

              {title && (
                <Text numberOfLines={1} style={[styles.titleText, titleStyle]}>
                  {title}
                </Text>
              )}
            </View>

            {description && (
              <Text style={[styles.descriptionText, descriptionStyle]}>
                {description}
              </Text>
            )}

            <View style={[styles.buttonContainer, footerStyle]}>
              <Button
                variant="secondary"
                style={[styles.button, styles.btnCancel]}
                onPress={handleClose}
              >
                <Text style={[styles.buttonText, styles.btnCancelText]}>
                  {cancelText}
                </Text>
              </Button>
              <Button
                variant="primary"
                style={[styles.button, styles.btnConfirm]}
                onPress={onConfirm}
              >
                <Text style={[styles.buttonText, styles.btnConfirmText]}>
                  {confirmText}
                </Text>
              </Button>
            </View>
          </View>
        )}
      </View>
    </ReactNativeModal>
  );
};

export default ConfirmModal;
